  // cubic-bezier(0.23, 1, 0.32, 1)
  .titlenames{
    font-size: 130px;
    font-weight: bold;
    font-family: $content-main-font;
    width: 100%;overflow: hidden;
    text-align: center;
    //border: 1px solid red;
    span{
      display: inline-block;
      // transform: translateY(150px);
      &:nth-of-type(1){
        transform: translateY(150px);
        animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0s forwards;
      }
      &:nth-of-type(2){
        transform: translateY(150px);
        animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0.2s 
        forwards;
      }
      &:nth-of-type(3){
        transform: translateY(150px);
        animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0.4s 
        forwards;
      }
      &:nth-of-type(4){
        transform: translateY(150px);
        animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0.6s 
        forwards;
      }
      &:nth-of-type(5){
        transform: translateY(150px);
        animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 0.8s 
        forwards;
      }
      &:nth-of-type(6){
        transform: translateY(150px);
        animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 1s 
        forwards;
      }
      &:nth-of-type(7){
        transform: translateY(150px);
        animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 1.2s 
        forwards;
      }
      &:nth-of-type(8){
        transform: translateY(150px);
        animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 1.4s 
        forwards;
      }
      &:nth-of-type(9){
        transform: translateY(150px);
        animation: title1 1.6s cubic-bezier(0.23, 1, 0.32, 1) 1.6s 
        forwards;
      }
      
    }
  }

@keyframes title1 {
  0%{
    transform: translateY(150px);
  }
  100%{
    transform: translateY(0px);
  }
}

// -----
// =--------
  .content_main{ 
    width: 13ch;
    animation: typing 2s steps(13) 1.8s , blink .5s step-end infinite alternate, noneblock 1.8s linear 0s;
    white-space: nowrap;
    overflow: hidden;
    border-right: 3px solid;
    font-size: 3em;
    //font-weight: 500;
    font-family: $h1-font;
    display: flex;
    align-items: center;
    //opacity: 0;
    span{
      margin: 0 10px;
    }
  }

  @keyframes noneblock {
    0%{width: 0}
    100%{width: 0}
  }

  @keyframes typing {
    from {
      width: 0
    }

  }
      
  @keyframes blink {
    50% {
      border-color: transparent
    }
  }


.mainP{
  position: relative;bottom: -10px;
  font-family: $h1-font;
  color: $main-color;
  font-size: 25px;
  opacity: 0;
  transform: scale(1.2);
  animation: mainP 0.5s linear 4.5s forwards;
  &.oou{
    color: red;
  }
}
@keyframes mainP {
  0%{opacity: 0;
    transform: scale(1.2);}
  100%{opacity: 1;
    transform: scale(1.0);}
}

}
